import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';

@Component({
  selector: 'app-roles-and-permissions-list',
  templateUrl: './roles-and-permissions-list.component.html',
  styleUrls: ['./roles-and-permissions-list.component.scss']
})
export class RolesAndPermissionsListComponent implements OnInit {

  public roleHeader:Array<any>=[];
  public roleList:Array<any>=[];
  public isEnableCreate:boolean=true;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  @HostListener('window:navigateToPermission')
  navigateToPermission() {
    this.router.navigateByUrl('/ams/roles-permissions/set-permission');
  }

  constructor(private router:Router) { }

  ngOnInit(): void {   
     var cellsrenderer = (row, column, value) => {
    return '<div class="jqx-custom-inner-cell">' + value + '</div>';
  }

  var columnrenderer = (value) => {
    return '<div style="padding: 14px">' + value + '</div>';
  }
    this.roleHeader = [{
      text: 'sno',
      datafield: 'sno',
      width: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Role Name',
      datafield: 'roleName',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Description',
      datafield: 'description',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    }, {
      text: 'set permission',
      cellsalign: 'center',
      align: 'center',
      width: 200,
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" onClick="navigateToPermission()" class="btn mat-primary">Set Permissions</a></div>'
      },
      renderer: columnrenderer
    }, 
     {
      text: 'action',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editTicketEvent(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEvent(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];

    let localData=[
    {
      sno:1,
      roleName:'Admin',
      description:'Admin Having All the Provisions'
    },
    {
      sno:2,
      roleName:'Staff',
      description:'Staff Having All the Provisions'
    }
    ]
 
     let gridSourceData = {
       localdata: localData,
       datatype: "array"
     }
 
     this.roleList = new jqx.dataAdapter(gridSourceData);
     this.isEnableCreate = true;
  }

}

function navigateToPermission(){
  var event = new CustomEvent('navigateToPermission')
  window.dispatchEvent(event);
}
(window as any).navigateToPermission = navigateToPermission;
